import React from 'react';
import ReactDOM from 'react-dom';
import { Button, Div, Root, CellButton, View, Panel, PanelHeader, Header, Group, Cell } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import {questions} from './data/data';
import ReactImageMagnify from 'react-image-magnify';


class Quiz extends React.Component {
    constructor(props) {
      super(props);
  
      this.state = {
        activeView: 'introView',
        currentQuestion: questions[0] 
      }
    }
  
    render() {
      return (
        <Root activeView={this.state.activeView}>
          <View activePanel="introPanel" id="introView">
            <Panel id="introPanel">
              <PanelHeader>Гобелены</PanelHeader>
              <Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray'}}>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit.
              Sed a sollicitudin lectus, a commodo sapien.
              Vivamus a urna leo.
              Integer iaculis dignissim urna, sit amet vestibulum diam bibendum a.
              Donec eu arcu ut augue porttitor faucibus.
              Vestibulum nec pretium tortor, sit amet congue nunc.
              Aenean ullamcorper ex sem, sed interdum quam consequat et.
              Vestibulum a ex non diam fringilla feugiat.
              Nunc eu tellus sed leo elementum cursus.
              Mauris blandit porta egestas.
              </Div>
              <Group>
                {/* <CellButton onClick={ () => this.setState({ activeView: 'view2' }) }> */}
                <CellButton onClick={ () => this.setState({ activeView: 'questionView'})}>
                 Пройти тест 
                </CellButton>
              </Group>
            </Panel>
          </View>
          <View activePanel="questionPanel" id="questionView">
            <Panel id="questionPanel">
              <PanelHeader>Это гобелен?</PanelHeader>
              <Group>
                  <Div style={{display: 'flex'}}>
                   <ReactImageMagnify
                   style={{
                     width: "90%",
                     maxWidth: '600px',
                     display: 'block',
                     marginLeft: 'auto',
                     marginRight: 'auto'
                   }}
                   {...{
                       smallImage: {
                           src: this.state.currentQuestion.image,
                           isFluidWidth: true,
                           sizes: '(max-width: 480px) 100vw, (max-width: 1200px) 30vw, 360px',
                       },
                       largeImage: {
                         src: this.state.currentQuestion.image,
                         width: 1000,
                         height: 1000
                       },
                       lensStyle: {backgroundColor: 'rgba(0,0,0,.6)'},
                       enlargedImagePosition: 'over'
                       }}
                ></ReactImageMagnify>
                  </Div>
                  <Div style={{
                      display: 'flex',
                      maxWidth: "550px",
                      marginLeft: 'auto',
                      marginRight: 'auto'
                    }}>
                      <Button size="m" stretched style={{marginRight: 8}}>Да</Button>
                      <Button size="m" stretched>Нет</Button>
                  </Div>
              </Group>
            </Panel>
          </View>
          <View activePanel="answerPanel" id="answerView">
            <Panel id="answerPanel">
              <PanelHeader>View 1</PanelHeader>
              <Group>
                <CellButton onClick={ () => this.setState({ activeView: 'view2' }) }>
                  Open View 2
                </CellButton>
              </Group>
            </Panel>
          </View>
          <View header activePanel="panel2.1" id="view2">
            <Panel id="panel2.1">
              <PanelHeader>View 2</PanelHeader>
              <Group>
                <CellButton onClick={ () => this.setState({ activeView: 'view1' }) }>
                  Back to View 1
                </CellButton>
              </Group>
            </Panel> 
          </View> 
        </Root>
      )
    }
  }
  
//   <Example />
ReactDOM.render(<Quiz />, document.getElementById('root'));